.header {
    border-radius: 15px;
    border: 1px solid rgb(253, 57, 8);
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}

.logo {
    width: 150px;
    border-radius: 50%;
}

.nav-items {
    padding: 8px 20px;
}

.nav-items>ul {
    display: flex;
    font-size: 24px;
    list-style-type: none;
}

.nav-items>ul>li {
    padding: 10px;
    margin: 10px;
    font-weight: bold;
}
.res-container {
    margin: 35px;
    display: flex;
    flex-wrap: wrap;
}
li:hover{
    color: rgb(253, 77, 8);
}
.res-card {
    margin: 5px;
    background-color: #f0f0f0;
    width: 200px;
    height: 400px;
    object-fit: cover; 
}
.span{
    padding: 10px;
}
.res-card:hover {
    border: 1px solid black;
    cursor: pointer;
}

.Search {
    padding: 10px;
}
.res-logo {
    width: 100%;
    height: 150px;
    object-fit: cover; 
    border-radius: 8px; 
}
.rating {
    display: flex;
}
.filter-btn{
        cursor: pointer;
        margin-top: 20px;
        width: 160px;
        font-weight: bold;
        color: aliceblue;
        line-height: 50px;
        background-color: rgb(253, 77, 8);
        border-radius: 15px;
        border: 1px solid rgb(253, 57, 8);
        box-shadow: 0 10px 15px rgb(250, 120, 69);
        transition: all 0.2s;
        margin: 10px;
}
    

.filter-btn:hover {
        background-color: white;
        box-shadow: 0 2px 15px rgb(253, 120, 69);
        border: 1px solid rgb(253, 57, 8);
        color: rgb(253, 57, 8);
}

 .log{
    cursor: pointer;
    width: 80px;
    height: 30px;
    font-weight: bold;
    color: aliceblue;
    background-color: rgb(253, 77, 8);
    border-radius: 10px;
    border: 1px solid rgb(253, 57, 8);
    box-shadow: 0 10px 15px rgb(250, 120, 69);
    transition: all 0.2s;
} 
.log:hover {
    background-color: white;
    box-shadow: 0 2px 15px rgb(253, 120, 69);
    border: 1px solid rgb(253, 57, 8);
    color: rgb(253, 57, 8);
}
.search {
    justify-content: center;
    margin: 20px 0;
    padding: 10px;
}

.search input {
    width: 500px;
    padding: 10px 15px;
    font-size: 20px;
    border: 1px solid rgb(253, 77, 8);
    border-radius: 5px;
    outline: none;
    transition: all 0.3s ease;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.search input::placeholder {
    color: #aaa;
}

.search input:focus {
    border-color: rgb(253, 77, 8);
    box-shadow: 0 2px 15px rgb(253, 120, 69);
    background-color: #f9f9f9;
}
.search-icon {
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    color: #aaa;
    font-size: 18px;
    pointer-events: none; /* Prevent the icon from blocking clicks on the input */
}
